import uniRequest from 'uni-request';
export default {
	data() {
		return {
			orderId:'',
			code:true,
		}
	},
	onLoad(option){
		if(uni.getStorageSync('userInfo').code !== ''){
			this.code = true
		}else{
			this.code = false
		}
		console.log(JSON.parse(option.payType))
		this.payType = JSON.parse(option.payType)
		this.orderId = JSON.parse(option.orderId)
	},
	methods: {
		async goPay(payType){
			if(!this.code){
				if(payType === 2){
					const res = await uniRequest({
						url: '/orders/'+this.orderId+'/payment/?mobile=1',
						method: 'get',
						headers: {
							Authorization: 'JWT ' + uni.getStorageSync('userInfo').token
						},
					}).then(res => {
						console.log(res.data)
						location.href = res.data.alipay_url
					}).catch(error => {
						console.log(error.data)
					})
				}else if(payType === 8){
					const res = await uniRequest({
						url: '/wechat/h5/orders/'+this.orderId+'/payment/',
						method: 'get',
						headers: {
							Authorization: 'JWT ' + uni.getStorageSync('userInfo').token
						},
					}).then(res => {
						console.log(res.data)
						location.href = res.data.mweb_url
					}).catch(error => {
						console.log(error.data)
						
					})
				}
			}else{
				if(payType === 5){
					const res = await uniRequest({
						url: '/orders/'+this.orderId+'/payment/?mobile=1',
						method: 'get',
						headers: {
							Authorization: 'JWT ' + uni.getStorageSync('userInfo').token
						},
					}).then(res => {
						console.log(res.data)
						location.href = res.data.alipay_url
					}).catch(error => {
						console.log(error.data)
					})
				}
			}
			
		}
	}
}
